}

.filter {
display: grid;
grid-template-columns: 2fr 2fr 2fr;
align-items: center;
grid-gap: 4rem;
padding: 1rem;
line-height: 1.8;
margin-bottom: 1rem;
border-radius: 5px;
margin: 2rem 2rem 0 2rem;
}





