/* UserList.js --> für den Abstand  */

.grid-one {
  grid-area: one;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 1rem;
}

.profile-button{
  grid-area: one ;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  grid-gap: 1rem;
  
}

.grid-one img {
  width: 250px;
}

.grid-one .icons a {
  color: #192534;
  color: var(--dark-color);
  margin: 0 0.3rem;
}

.icons a {
  color: grey;
  margin: 0 0.3rem;
}

.grid-one .icons a:hover {
  color: #192534;
  color: var(--dark-color);
}



/* MessageForm & Container*/ 
.message-form {
  grid-area: one ;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 0.5rem;
  padding: 0 3rem;
}


/* show Profile --> goals */ 
.grid-two {
  grid-area: two;
  text-align: center;
  border-radius: 0 0 0.5rem 0.5rem ;
}

.grid-two .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 0.2rem;
}

.grid-three {
  grid-area: three ;
  border-radius: 0.2rem;
}

.grid-three{
  grid-area: three ;
  border-radius: 0.2rem;
}

.grid-four {
  grid-area: four;
  border-radius: 0.5rem;     
}

.grid-three h2,
.grid-four h2 {
  margin-bottom: 1rem;
}

.grid-three > div,
.grid-four > div {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: #ccc 1px dotted;
}

.grid-three > div:last-child,
.grid-four > div:last-child {
  border: 0;
}

.grid-three p,
.grid-four p {
  margin: 0.5rem 0;
}
 

.profile ul {
  display: inline-flex;
  justify-content: space-around;
  padding: 0, 2rem;
  border-radius: 0 0rem 2rem 2rem;
}




