.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff;
}


.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
  border: rgb(228, 228, 228) solid 1px; 
}

.btn:hover {
  opacity: 0.8;
}

.bg-light,
.badge-light {
  border: #fbfbfb;
  border-radius: 2rem;
}

/* Forms */
.form .form-group {
  margin: 1.2rem 10rem;
}


.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type='text'],
.form input[type='password'],
.form input[type='date'],
.form input[type='number'],
.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  padding-left:0.8rem;
  font-size: 1.1rem;
  border:  1px solid #ccc;
  transform: capitalize;
  border-radius:0.5rem;
}

.form input[type='submit'],
button {
  font: inherit;
}

.form input:hover[type='text'],
.form input:hover[type='password'],
.form input:hover[type='date'],
.form input:hover[type='number'],
.form input:hover[type='select'],
.form select:hover
 {
  color:var(--dark-color);
  font-weight:bold;
}

.form textarea:hover{ 
  color:var(--dark-color);
}



